/* Extra Small Devices, Phones */
@media only screen and (min-width: 480px) {  
  .navbar-nav .nav-link {
    font-size: 14px;
    // padding: 0;
  }

  .services .heading {
    position: static;
    text-align: center;
    width: auto;
    padding-top: 30px;
    margin-bottom: 60px;
  }

  .services .heading h3 {
    font-size: 50px;
    line-height: 50px;
  }

  .diamond-grid {
    padding-top: 0;
    margin-left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .grid_show .diamond-box {
    transform: rotate(45deg) scale(1);
  }

  .diamond-group {
    transform: rotate(0deg);
  }

  .diamond-box {
    transform: rotate(45deg) scale(0);
    margin-right: 90px;
  }

  .service_schema_right {
    display: block;
  }
}

@media only screen and (min-width: 576px) {  
  .header .navbar-brand .logo-text, .header .navbar-light .navbar-nav .nav-link {  
    font-size: 12px;
  }
  .navbar-nav .dropdown-menu {
    background: #1b67ce;
    background-image: linear-gradient(150deg,#2d3fb1 27%,#0690e2 70%);
    border: none;
    margin-top: 6px;
    padding: 0;
  }
  .dropdown-menu .dropdown-item {
    font-size: 16px;
    padding: 20px 30px;
    font-weight: bold;
    color: #ffffff;
    border-bottom: 1px solid #095b9c;
  }
  .dropdown-menu .dropdown-item:hover, .dropdown-item:focus {
    color: #ffffff;
    background: #6b07ca;
  }  
}


@media screen and (min-width: 576px) and (max-height: 420px){  
  .section {
    padding: 20px 0;
  }
  .section .heading {
    padding-top: 0;
    margin-bottom: 30px;
  }
  .section .heading h3 {
    font-size: 40px;
    line-height: 40px;
  }

  .hero-content {
     margin-bottom: 0px;
  } 

  .hero-content h1 {
    font-size: 50px;
    line-height: 40px;
  }

  .hero-content span {
    font-size: 24px;
    line-height: 24px;
  }
  .order-link {    
    font-size: 14px;
    line-height: 20px;
    padding: 14px 20px;
    margin-top: 14px;
  }
  .arrow-link a {    
    width: 40px;
    height: 50px;
    margin: 0px auto 0;
  }
  .diamond-grid {
    margin-left: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 80px;
  }

  .diamond-group {
    flex-direction: column;
    transform: rotate(-45deg);
    margin-left: 65px;
    margin-bottom: 0;
  }

  .diamond-group:last-child {
    margin-right: 0;
  }

  .grid_show .diamond-box {
    width: 110px;
    height: 110px;
    transform: rotate(90deg) scale(1);
    margin-right: 0px;
    margin-bottom: 14px;
  }
  
  .diamond-title {    
    line-height: 14px;
  }

  .diamond-box:last-child {
    margin-bottom: 0;
  }

  .diamond-group:last-child .diamond-box:first-child {
    margin-bottom: 125px;
  }
  
  .diamond-group:last-child .diamond-box:last-child {
    position: absolute;
    left: -247px;
    top: 0px;
  }
  .diamond-box img {
    width: 110px;
  }
  .diamond-title {
    font-size: 15px;
  }
  .service_schema_left, .service_schema_right {
    width: 160px;
  }
  .about-text p {
    font-size: 16px;
    margin-bottom: 20px;
  }
  .adv-pics {    
    flex-direction: row;
    justify-content: space-between;
  }
  .adv-item {
    width: 240px;
    height: 160px
  }
  .adv-pic-1 {
    margin-bottom: 0;
  }

}

/* Small Devices, Tablets */
@media only screen and (min-width: 768px) {  
  .header .navbar-brand .logo-text, .header .navbar-light .navbar-nav .nav-link {  
    font-size: 14px;
  }
  .dropdown-menu .dropdown-item {
    color:#fff;
    padding: 20px 30px;
  }

  .hero-content h1 {
    font-size: 90px;
    line-height: 90px;
  }
  .diamond-grid {
    margin-left: 0;
  }

  .grid_show .diamond-box {
    transform: rotate(45deg) scale(1);
  }

  .diamond-grid {
    margin-left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .diamond-group {
    flex-direction: row;
    margin-right: 0px;
    margin-bottom: 90px;
  }

  .diamond-group:last-child {
    margin-right: 0;
  }

  .diamond-box {
    transform: rotate(45deg) scale(0);
    margin-right: 90px;
    margin-bottom: 0px;
  }
  
  .diamond-box:last-child {
    margin-right: 0;
  }

  .service_schema_left,
  .service_schema_right {
    width: 235px;
  }

  .contact {
    text-align: left;
  }

  .contact-link {
    margin-left: 40px;
    margin-bottom: 30px;
  }

  .contact #vk_subscribe {
    margin: 0;
    margin-top: 15px;
  }
  .bonus-item {
    width: 240px;
    height: 100px;
    margin: 0;
  }

  .order-form {
    margin-bottom: 30px;
  }

  .order-form h3 {
    text-align: left;
  }

  .page-hero {
    background-position: left center;
  }
  .page-heading h2 {
    font-size: 44px
  }
  
}

/* Medium Devices, Desktops */
@media only screen and (min-width: 992px) {
  .navbar-nav .nav-link {
    font-size: 14px;
    padding: 0;
  }
  .diamond-grid {
    margin-left: 0;
  }

  .grid_show .diamond-box {
    transform: rotate(90deg) scale(1);
  }

  .diamond-grid {
    margin-left: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 80px;
  }

  .diamond-group {
    flex-direction: column;
    transform: rotate(-45deg);
    margin-right: 110px;
    margin-bottom: 0;
  }

  .diamond-group:last-child {
    margin-right: 0;
  }

  .diamond-box {
    width: 165px;
    height: 165px;
    transform: rotate(90deg) scale(0);
    margin-right: 0px;
    margin-bottom: 30px;
  }
  
  .diamond-title {    
    line-height: 30px;
  }

  .diamond-box:last-child {
    margin-bottom: 0;
  }

  .diamond-group:last-child .diamond-box:first-child {
    margin-bottom: 192px;
  }
  
  .diamond-group:last-child .diamond-box:last-child {
    position: absolute;
    left: -390px;
    top: 0px;
  }

  .about-text p {
    font-size: 17px;
  }

  .advantages .heading {
    font-size: 54px;
    line-height: 44px;
  }

  .bonus-item {
    width: 310px;
    height: 130px;
  }

  .option-info {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
}

/* Large Devices, Wide Screens */
@media only screen and (min-width: 1200px) {
  .diamond-grid{
    margin-bottom: 150px;
  }

  .diamond-group {
    margin-right: 140px;
  }
  
  .diamond-box {
    width: 200px;
    height: 200px;
    margin-bottom: 40px;
  }

  .diamond-group:last-child .diamond-box:first-child {
    margin-bottom: 240px;
  }

  .diamond-group:last-child .diamond-box:last-child {
    left: -480px;
    top: 0px;
  }


  .service_schema_left,
  .service_schema_right {
    width: 255px;
  }

  .bonus-item {
    width: 360px;
    height: 150px;
  }
}

@media only screen and (min-width: 1440px) {
  .loader {
    width: 380px;
    height: 380px;
  }
  .diamond-group {
    margin-right: 150px;
  }

  .diamond-group:last-child .diamond-box:first-child {
    margin-bottom: 270px;
  }

  .diamond-group:last-child .diamond-box:last-child {
    left: -550px;
    top: 0;
}

  .diamond-box {
    width: 240px;
    height: 240px;
    margin-bottom: 40px;
  }

  .diamond-box img {
    position: absolute;
    width: 210px;
    height: 160px;
    top: 46%;
  }
  .diamond-title {
    font-size: 28px;
  }

  .service_schema_left,
  .service_schema_right {
    width: 15%;
  }

  .about-text p {
    font-size: 22px;
  }

  .option-desc {
    font-size: 22px;
  }
  .option-price {
    font-size: 24px;
  }
  .bonus-item {
    width: 350px;
    height: 145px;
    margin: 0;
  }
}

@media only screen and (min-width: 2060px) {
  .header {
    padding: 20px 0;
  }
  .header .navbar-brand {
    font-size: 20px;
  }

  .navbar-nav .nav-link {
    font-size: 22px;
    line-height: 24px;
  }
  .hero-content h1 {
    font-size: 130px;
    line-height: 120px;
  }
  .hero-content span {
    display: block;
    font-size: 45px;
    line-height: 40px;
  }
  .order-link {
    font-size: 28px;
    line-height: 20px;
    padding: 35px 50px;
  }
  .arrow-link a {
    width: 100px;
    height: 140px;
  }
}
