@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/OpenSans/open-sans-v17-latin_cyrillic-regular.eot"); /* IE9 Compat Modes */
  src: local("Open Sans Regular"), local("OpenSans-Regular"),
    url("../../fonts/OpenSans/open-sans-v17-latin_cyrillic-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("../../fonts/OpenSans/open-sans-v17-latin_cyrillic-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("../../fonts/OpenSans/open-sans-v17-latin_cyrillic-regular.woff") format("woff"),
    /* Modern Browsers */
      url("../../fonts/OpenSans/open-sans-v17-latin_cyrillic-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../../fonts/OpenSans/open-sans-v17-latin_cyrillic-regular.svg#OpenSans")
      format("svg"); /* Legacy iOS */
}
/* open-sans-700 - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/OpenSans/open-sans-v17-latin_cyrillic-700.eot"); /* IE9 Compat Modes */
  src: local("Open Sans Bold"), local("OpenSans-Bold"),
    url("../../fonts/OpenSans/open-sans-v17-latin_cyrillic-700.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("../../fonts/OpenSans/open-sans-v17-latin_cyrillic-700.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("../../fonts/OpenSans/open-sans-v17-latin_cyrillic-700.woff") format("woff"),
    /* Modern Browsers */ url("../../fonts/OpenSans/open-sans-v17-latin_cyrillic-700.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../../fonts/OpenSans/open-sans-v17-latin_cyrillic-700.svg#OpenSans")
      format("svg"); /* Legacy iOS */
}

