@import "./includes/vars";

.footer {
  position: relative;
  background: #101f40;
  padding: 40px 0 0 10px;
  border-top: 2px solid #0091d6;
  color: #fff;
  z-index: 20;
  overflow-x: hidden;
}

.contact {
  opacity: 0;
  display: flex;
  flex-direction: column;
}

.contact.animated {
  opacity: 1;
}

.contact-heading {
  font-size: 40px;
  line-height: 30px;
  margin-bottom: 30px;
}

.contact-link {
  display: inline-block;
  font-size: 20px;
  color: #fff;
  text-decoration: none;
  margin: 0 auto 30px auto;
  text-align-last: left;
  position: relative;
}
.contact-link:before {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  position: absolute;
  left: -40px;
  top: 50%;
  transform: translateY(-50%);
}

.contact-phone:before {
  background: url("../img/telephone.svg");
  background-size: cover;
}

.contact-email:before {
  background: url("../img/email.svg");
  background-size: cover;
}

.footer .order-form {
  opacity: 0;
  margin-bottom: 30px;
}

.footer .order-form.animated {
  opacity: 1;
}

.order-form h3 {
  font-size: 22px;
  margin-bottom: 20px;
  text-align: center;
}

.footer .order-form .message {
  resize: none;
  min-height: 105px;
}

.contact {
  text-align: center;
}

.contact #vk_subscribe {
  margin: 0 auto;
  margin-top: 20px;
}

.footer-bottom {
  padding: 10px 0;
}
.footer-bottom p {
  text-align: center;
  color: #ffffff;
}
