@import "./includes/vars";
@import "./includes/fonts";
@import "./includes/libs";

@import "header";
@import "footer";

* {
  transition: none;
}

html {
  font-size: 14px;
}

html,
body {
  height: 100%;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  background: url("/img/bkg-tile.jpg");
  font-family: $main-font;
  font-weight: 300;
  line-height: 1.65;
  color: $text;
}

.main {
  flex: 1;
  overflow: hidden;
}

a {
  color: $text;
  font-size: 1rem;
  font-weight: 300;
  transition: 0.3s;
  line-height: 1rem;
  &:hover {
    color: $accent;
    text-decoration: underline;
  }
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $heading-font;
  font-style: normal;
  font-weight: 700;
}

p {
  font-weight: 300;
  font-style: normal;
  margin-bottom: 0;
  font-size: 1.2rem;
  color: $text;
}

.section-cover {
  &::before {
    content: "";
    display: block;
    position: absolute;
    opacity: 0.8;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
}

.h-100 {
  height: 100%;
}

.img-100 {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

.icon {
  display: inline-block;
  height: 3rem;
  width: 3rem;
  fill: $accent;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.delay-100 {
  animation-delay: 100ms;
}

.delay-200 {
  animation-delay: 200ms;
}

.delay-300 {
  animation-delay: 300ms;
}

.delay-400 {
  animation-delay: 400ms;
}

.delay-500 {
  animation-delay: 500ms;
}

.delay-600 {
  animation-delay: 600ms;
}

.delay-700 {
  animation-delay: 700ms;
}

.delay-800 {
  animation-delay: 800ms;
}

.delay-900 {
  animation-delay: 900ms;
}

.delay-1000 {
  animation-delay: 1000ms;
}

.page {
  display: none;
}
.page-active {
  display: block;
}

.services {
  background-position: 0px -10px;
  background-size: cover;
  padding-top: 0;
  position: relative;
}

.services .heading {
  width: 160px;
  position: absolute;
  right: 10px;
  top: 8px;
  text-align: right;
  opacity: 0;
}

.services .heading h3 {
  font-size: 40px;
  line-height: 40px;
}

.heading.animated {
  opacity: 1;
}

.diamond-grid {
  margin-left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 180px;
}

.diamond-group {
  display: flex;
  flex-direction: row;
  transform: rotate(45deg);
  margin-bottom: 92px;
  position: relative;
}

.diamond-group:last-child {
  margin-bottom: 0;
}

.diamond-box {
  width: 140px;
  height: 140px;
  margin-right: 20px;
  background-color: #fff;
  transform: rotate(0deg) scale(0);
  overflow: hidden;
  position: relative;
  box-shadow: 10px 4px 20px -10px #373942;
  border-left: 3px solid #2a3da9;
  border-top: 3px solid #2a3da9;
  border-bottom: 3px solid #502d90;
  border-right: 3px solid #502d90;
  cursor: pointer;
  z-index: 100;
}


.diamond-box:after {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(126, 155, 220, 0.12);
  transition: background 0.5s;
  z-index: 0;
}

.diamond-box:hover,
.diamond-box:focus {
  border-color: #3b0b9a;
}

.diamond-box:hover:after,
.diamond-box:focus:after {
  background: rgba(19, 32, 68, 0.7);
}

.diamond-title {
  color: #fff;
  font-size: 17px;
  line-height: 20px;
  font-weight: 600;
  text-align: center;
  text-shadow: -2px -2px 0 #000;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  transition: opacity 0.5s;
  z-index: 10;
}

.diamond-box:hover .diamond-title,
.diamond-box:focus .diamond-title {
  opacity: 1;
  text-decoration: underline;
}

.diamond-box img {
  position: absolute;
  width: 160px;
  top: 48%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}

.diamond-box:last-child {
  margin-right: 0;
}

.grid_show .diamond-box {
  transition: transform 1s 0.5s;
  transform: rotate(0deg) scale(1);
}

.service_schema_left,
.service_schema_right {
  width: 284px;
  height: auto;
  position: absolute;
  top: -10px;
  z-index: -20;
}

.service_schema_left {
  left: 0px;
}
.service_schema_right {
  right: 0px;
  display: none;
}

.service_schema_middle {
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translate(-50%, 0px) rotate(180deg);
  width: 70%;
  z-index: -20;
}

.service_schema_left svg,
.service_schema_right svg {
  width: 100%;
}

.service_svg_line {
  stroke-dasharray: 500;
  stroke-dashoffset: 500;
  transition-duration: 4s;
  transition-property: stroke-dashoffset;
}

.service_svg_dot {
  transition-duration: 1s;
  transition-property: transform;
}

.svg_line,
.svg_circle {
  stroke: $accent;
}

.svg_circle_solid {
  fill: $accent;
  stroke: $accent;
}

.svg_anim_move .service_svg_line {
  stroke-dashoffset: 0;
}

.about {
  overflow: hidden;
}
.about .heading {
  opacity: 0;
}

.about-text {
  opacity: 0;
  animation-delay: 500ms;
}

.about-text.animated {
  opacity: 1;
}

.about-text p {
  font-size: 20px;
  margin-bottom: 20px;
}

.about #vk_groups {
  opacity: 0;
  animation-delay: 500ms;
}

.about #vk_groups.animated {
  opacity: 1;
}

.advantages {
  padding: 20px 0;
  color: #fff;
  overflow: hidden;
}

.advantages .heading {
  opacity: 0;
}
.advantages .heading h3 {
  font-size: 34px;
  line-height: 34px;
}

.adv-pics {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;
}

.adv-item {
  width: 280px;
  height: 180px;
  background-repeat: no-repeat;
  background-size: cover;
  border: 4px solid #5328c7;
}

.adv-pic-1 {
  background-image: url("../img/adv-pic-1.jpg");
  margin-bottom: 20px;
  opacity: 0;
}

.adv-pic-2 {
  background-image: url("../img/adv-pic-2.jpg");
  opacity: 0;
}

.adv-pic-1.animated,
.adv-pic-2.animated {
  opacity: 1;
}

.advantages .adv-option {
  font-size: 18px;
  padding-left: 70px;
  margin-bottom: 20px;
  color: $text;
  position: relative;
  opacity: 0;
}

.adv-option.animated {
  opacity: 1;
}

.advantages .adv-option:before {
  content: "\2714";
  display: block;
  width: 32px;
  height: 32px;
  font-size: 22px;
  line-height: 30px;
  color: #fff;
  text-align: center;
  position: absolute;
  left: 10px;
  background: #5328c7;
  border-radius: 30px;
}

.bonuses .heading {
  opacity: 0;
}

.bonus-item {
  display: block;
  width: 300px;
  height: 125px;
  background-size: cover;
  background-repeat: no-repeat;
  margin: 0 auto 10px auto;
  opacity: 0;
}
.bonus-item.animated {
  opacity: 1;
}

.for-subs {
  background-image: url("../img/for-subs.jpg");
  animation-delay: 500ms;
}

.for-third {
  background-image: url("../img/for-third.jpg");
  animation-delay: 700ms;
}

.for-older {
  background-image: url("../img/for-older.jpg");
  animation-delay: 900ms;
}

.page-hero {
  height: 60vh;
  margin-top: 50px;
  background-size: cover;
  background-color: #647fac;
  background-position: -140px;
  background-repeat: no-repeat;
  position: relative;
  overflow-x: hidden;
}

.page-content #c_digits {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
}

.page-heading {
  color: #fff;
  text-align: right;
  text-shadow: 5px 3px 0 #1b223e;
  position: absolute;
  right: 20px;
  bottom: 40px;
  opacity: 0;
  animation-delay: 500ms;
}
.page-heading.animated {
  opacity: 1;
}

.page-heading h2 {
  font-size: 34px;
}

.service-option {
  display: flex;
  background: #fff;
  border: 3px solid #80a6d6;
  padding: 10px 0;
  border-bottom: none;
  align-items: center;
}
.service-option:last-child {
  border-bottom: 2px solid #80a6d6;
}

.option-img {
  width: 100px;
  height: 100px;
  overflow: hidden;
  margin-left: 10px;
  flex-shrink: 0;
}

.option-img img {
  width: 100%;
  margin-top: 50%;
  transform: translateY(-50%);
}

.option-title {
  padding: 20px 0px;
  font-size: 20px;
  border-left: none;
  border-right: none;
}

.option-desc {
  flex: 1;
  padding: 0 20px;
  font-size: 18px;
}

.option-price {
  font-size: 19px;
  padding: 0 20px;
}

.description {
  position: relative;
  width: 80%;
  margin-top: 30px;
  padding: 15px;
  background: #fff;
  margin: 30px auto 0 auto;
}

.order-submit {
  margin-right: 20px;
}

.order-submit.disabled, .order-submit:disabled {
  color: #a09c9c;
  background-color: #5b626b;
  border-color: #484c50;
}

.submit-status {
  flex: 1;
  opacity: 0;
  padding: 8px;
  color: #fff;
  text-align: center;
  border-radius: 4px;
}

.err-message {
  display: none;
  color: red;
}

// Всегда в конце
@import "./includes/media";
