@import "./includes/vars";

// ---- first screen start ---

.header {
  padding: 10px 0;
  background: #283dbb;
  box-shadow: 0px -2px 20px 0px #000000bf;
  background-image: linear-gradient(110deg, #16247b 27%, #0690e2 70%);
}

.navbar {
  padding: 0;
}

.navbar .navbar-brand {
  padding: 0;
  display: flex;
  align-items: center;
}

.navbar .navbar-brand .logo {
  display: block;
  width: 45px;
  height: 45px;
  background: url("../img/logo.png") no-repeat;
  background-size: contain;
  margin-right: 10px;
}

.header .navbar-light .navbar-nav .nav-link,
.header .navbar-brand .logo-text {
  color: #fff;
  font-weight: 600;
}

.header .navbar-light .navbar-nav .nav-link:hover {
  color: #09ff99;
}

.header .navbar-collapse {
  flex-grow: 0;
  margin-left: auto;
}

.navbar-nav .nav-link {
  font-size: 18px;
  padding: 14px 0;
  cursor: pointer;
}

.navbar-nav .dropdown-menu {
  background: #1b67ce;
  background-image: linear-gradient(150deg,#2d3fb1 27%,#0690e2 70%);
  border: none;
  margin-top: 6px;
  padding: 0;
}

span.dropdown-toggle {
  line-height: 14px;
}

.dropdown-menu .dropdown-item {
  font-size: 16px;
  padding: 20px 30px;
  font-weight: bold;
  color: #ffffff;
  border-bottom: 1px solid #095b9c;
}

.dropdown-menu .dropdown-item:hover, .dropdown-item:focus {
  color: #ffffff;
  background: #6b07ca;
}

.navbar-toggler.hamburger {
  padding: 5px 0 0 0;
}

.hamburger-inner,
.hamburger-inner::before,
.hamburger-inner::after {
  background-color: #fff;
}

.loader-overlay {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  overflow: hidden;
  z-index: 2000;
  background: #16247b;
}

.loader {
  width: 250px;
  height: 250px;
}

.circular {
  width: 100%;
  height: 100%;
}

.circular #circle-2 {
  transform-origin: 50% 50%;
  stroke-dashoffset: -23;
  stroke-dasharray: 48;
  animation: rotate 2s cubic-bezier(0.165, 0.84, 0.44, 1) infinite;
}

.circular #circle-1 {
  transform-origin: 50% 50%;
  stroke-dashoffset: -20;
  stroke-dasharray: 42;
  animation: rotate 1568ms linear infinite;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.section {
  padding: 40px 0;
  text-align: left;
  position: relative;
}

.section .heading {
  text-align: center;
  margin-bottom: 30px;
  color: #2a34ad;
  text-shadow: 3px 2px 0px #d6cfcf;
  position: relative;
}

.section .heading h3 {
  font-size: 45px;
}

.hero {
  position: relative;
  background-color: #003b75;
  background-image: url("../img/hero-bg.jpg");
  background-repeat: no-repeat;
  background-size: auto 130%;
  background-position: 50% 0px;
  background-attachment: fixed;
  height: 130vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-content {
  width: 100%;
  color: #ffffff;
  text-align: center;
  margin-bottom: 100px;
}

.hero-content:after {
  content: "";
  position: absolute;
  width: 75%;
  height: 120px;
  background: rgba(0, 0, 0, 0.35);
  top: 0px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 0 10px 20px rgba(0, 0, 0, 0.35);
  border-radius: 50px;
  opacity: 0;
  transition: opacity 1s 800ms;
}

.animated.hero-content:after {
  opacity: 1;
}

.hero-content h1 {
  font-size: 60px;
  line-height: 60px;
  font-family: "Dexter", sans-serif;
  text-shadow: 3px 2px 0px #1fb7ff, 5px -1px 0px #593dff;
  letter-spacing: 5px;
  opacity: 0;
  animation-duration: 1s;
  animation-delay: 300ms;
  position: relative;
  z-index: 100;
}

.hero-content h1.animated {
  opacity: 1;
}

.hero-content span {
  display: block;
  font-size: 30px;
  line-height: 35px;
  text-shadow: 2px 1px 10px #00b8ff;
  font-weight: 600;
  opacity: 0;
  animation-duration: 900ms;
  animation-delay: 1400ms;
  position: relative;
  z-index: 100;
}

.hero-content span.animated {
  opacity: 0;
}

.order-link {
  display: inline-block;
  color: #ffffff;
  background: #a20262;
  font-size: 17px;
  line-height: 20px;
  padding: 20px 30px;
  margin-top: 20px;
  text-transform: uppercase;
  text-decoration: none;
  border-radius: 22px;
  box-shadow: 0px 3px 0 0 #320042;
  transform: scale(1);
  transition: 0.8s;
  background-image: linear-gradient(180deg, #420a75 27%, #3915c7 70%);
  opacity: 0;
  animation-delay: 2100ms;
  position: relative;
  z-index: 100;
}

.order-link.animated {
  opacity: 1;
}

.order-link:hover {
  text-decoration: none;
  color: #fff;
  transform: scale(1.1);
  text-shadow: 0px 0px 5px #31c4ed;
}

.arrow-link {
  display: block;
  opacity: 0;
  animation-delay: 2100ms;
}

.arrow-link a {
  display: block;
  width: 50px;
  height: 70px;
  margin: 30px auto 0 auto;
  background: url("../img/icon-arrow-down.svg");
  background-repeat: no-repeat;
  background-size: cover;
  animation: hero-arrow-bounce 0.8s infinite backwards ease;
}
.arrow-link.animated {
  opacity: 1;
}

@keyframes hero-arrow-bounce {
  0% {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(30%);
  }
  100% {
    transform: translateY(0%);
  }
}

// ---- first screen end ---